import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import { editUser } from '../store/apiCalls';
import { useDispatch } from "react-redux";
import { Checkbox, FormControlLabel, MenuItem, Select } from '@mui/material';
import { useNavigate, useParams } from 'react-router';
import { userRequest } from '../requestMethods';
import { setLoading } from '../store/userSlice';
import { useState } from 'react';

export default function EditUser() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [user, setUser] = React.useState({})
  const [level, setlevel] = React.useState("1")
  const [post, setpost] = React.useState("1")
  const [p, setP] = useState("A")


  let { id } = useParams();

  const getData = async (uri) => {
    dispatch(setLoading(true));
    try{
        const { data } = await userRequest.get(uri);
        setUser(data);
        setlevel(data.level)
        setpost(data.post)
        setP(data.p)
        dispatch(setLoading(false));

    }catch(e){
        dispatch(setLoading(false));
    }

};
React.useEffect(() => {
    getData(`/user2/find/${id}`);
}, []);

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log({
      name: data.get('name'),
      post: post,
      level: level,
      p: p,
      tel: data.get('tel'),
    });
      editUser(dispatch,{
        name: data.get('name'),
        post: post,
        level: level,
        p: p,
        tel: data.get('tel'),
      },navigate,id)
  };
  

  return (
    user.name &&
      <Container component="main" maxWidth="md">
        <Box
          sx={{
            marginTop: 1,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
            <h1 className='title' >Edit User</h1>
          <Box component="form" onSubmit={handleSubmit}  sx={{ mt: 1 }}>
          <TextField
              margin="normal"
              required
              fullWidth
              id="name"
              label="Name"
              defaultValue={user.name}
              name="name"
              autoComplete= "no"

              inputProps={{
                autoComplete: ''
             }}
            />
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={post}
              label="Post"
              name="post"
              onChange={e => setpost(e.target.value)}
            >
              <MenuItem value={1}>Qualite </MenuItem>
              <MenuItem value={2}>Maintenance</MenuItem>
              <MenuItem value={3}>Production</MenuItem>
              <MenuItem value={4}>Logistique</MenuItem>
            </Select>
            <TextField
              margin="normal"
              required
              fullWidth
              name="tel"
              label="Phone Number"
              id="tel"
              defaultValue={user.tel}
            />

            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={level}
              label="Age"
              name="level"
              onChange={e => setlevel(e.target.value)}
            >
              <MenuItem value={1}>Technicien</MenuItem>
              <MenuItem value={2}>Superviseur</MenuItem>
              <MenuItem value={3}>Dous-Directeur</MenuItem>
              <MenuItem value={4}>Directeur</MenuItem>
            </Select>

            <div>

<Select
  labelId="demo-simple-select-label"
  id="demo-simple-select"
  value={p}
  label="Post"
  name="level"
  onChange={e => setP(e.target.value)}
>
  <MenuItem value={"A"}>A</MenuItem>
  <MenuItem value={"B"}>B</MenuItem>
  <MenuItem value={"C"}>C</MenuItem>
</Select>
 </div>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              className="blue"
            >
              Save
            </Button>
          </Box>
        </Box>
      </Container>
  );
}
