import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Container } from '@mui/system';
import { Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import { userRequest } from '../requestMethods';

import EditIcon from '@mui/icons-material/Edit';
import SearchAddBar from '../components/SearchAddBar';
import { useNavigate } from 'react-router';
import { setLoading, setNotification } from '../store/userSlice';
import { useDispatch } from 'react-redux';
import DeleteIcon from '@mui/icons-material/Delete';
import { deleteUser2 } from '../store/apiCalls';

// import { jsPDF } from "jspdf";
// import html2PDF from "jspdf-html2canvas";

// Default export is a4 paper, portrait, using millimeters for units


export default function His() {
    const [users, setUsers] = React.useState([])
    const [search, setsearch] = React.useState("")
    const navigate = useNavigate();
    const dispatch = useDispatch();
    // function pdf() {
    //     html2PDF(document.getElementById("InvoiceTemp"), {
    //         jsPDF: {
    //           format: "a4"
    //         },
    //         html2canvas: {
    //           imageTimeout: 15000,
    //           scale: 5,
    //           logging: true
    //         },
    //         imageQuality: 0.9,
    //         imageType: "image/jpeg",
    //         output: "generate.pdf"
    //       });
    // }
    // function pdf2() {
    //    window.print()
    // }

    const getData = async (uri) => {
        dispatch(setLoading(true));
        try {
            const { data } = await userRequest.get(uri);
            setUsers(data);
            dispatch(setLoading(false));

        } catch (e) {
            dispatch(setLoading(false));
        }
    };
    function searchH(text) {
        setsearch(text)
    }

    React.useEffect(() => {
        if (search.length >= 3) {
            getData(`/his/search/${search}`);
        } else if (search.length === 0) {
            getData(`/his?new`);
        }
    }, [search]);

    const [open, setOpen] = React.useState(false);
    const [id, setId] = React.useState("");

    const handleClickOpen = (id) => {
        setOpen(true);
        setId(id)

    };
    const deleteAgree = () => {
        deleteUser2(dispatch, id).then(d => {
            setUsers(users.filter(t => t._id != id))
            setId("")
        }).catch(e => console.log(e));
        setOpen(false);
    }

    const handleClose = () => {
        setOpen(false);
        setId("")
    };
    return (
        <Container component="main" maxWidth="xl" id="InvoiceTemp" >
{
    id && (
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {id.name}
                </DialogTitle>
                <DialogContent>
                    <Table sx={{ minWidth: 450 }} aria-label="a dense table">
                        <TableHead>
                            <TableRow>
                                <TableCell>Type</TableCell>
                                <TableCell align="center"> Temps </TableCell>
                                <TableCell align="center">Freq</TableCell>
                                <TableCell align="center">Alarm</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            
                                <TableRow
                                    key={34}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {"Qualite"}
                                    </TableCell>
                                    <TableCell align="center">{id.data.TQ}</TableCell>
                                    <TableCell align="center">{id.data.FQ}</TableCell>
                                    <TableCell align="center">{id.data.AQ}</TableCell>
                                </TableRow>
                                <TableRow
                                    key={36}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {"Production"}
                                    </TableCell>
                                    <TableCell align="center">{id.data.TP}</TableCell>
                                    <TableCell align="center">{id.data.FP}</TableCell>
                                    <TableCell align="center">{id.data.AP}</TableCell>
                                </TableRow>
                                <TableRow
                                    key={37}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {"Logistique"}
                                    </TableCell>
                                    <TableCell align="center">{id.data.TL}</TableCell>
                                    <TableCell align="center">{id.data.FL}</TableCell>
                                    <TableCell align="center">{id.data.AL}</TableCell>
                                </TableRow>
                                <TableRow
                                    key={37}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {"Maintenance"}
                                    </TableCell>
                                    <TableCell align="center">{id.data.TM}</TableCell>
                                    <TableCell align="center">{id.data.FM}</TableCell>
                                    <TableCell align="center">{id.data.AM}</TableCell>
                                </TableRow>
                            
                        </TableBody>
                    </Table>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Ok</Button>
                </DialogActions>
            </Dialog>

    )
}
            <Box
                sx={{
                    marginTop: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <h1 className='title' >History</h1>
                {/* <Button style={{backgroundColor: "orange"}} onClick={e => pdf()} variant="contained" endIcon={<EditIcon />}>
                                        pdf
                                    </Button>
                <Button style={{backgroundColor: "blue"}} onClick={e => pdf2()} variant="contained" endIcon={<EditIcon />}>
                                        pdf2
                                    </Button> */}
                <SearchAddBar searchHandler={searchH}  />
                <TableContainer component={Paper}>
                    <Table sx={{ minWidth: 650 }} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell align="Left">Ligne</TableCell>
                                <TableCell align="center">Created At</TableCell>
                                <TableCell align="center">Show More</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {users.map((row) => (
                                <TableRow
                                    key={row._id}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {row.name}
                                    </TableCell>
                                    <TableCell align="center">{row.createdAt}</TableCell>
                                    <TableCell align="center"><Button style={{ backgroundColor: "orange" }} onClick={e => handleClickOpen(row)} variant="contained" endIcon={<EditIcon />}>
                                        More
                                    </Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Box>
        </Container>
    );
}
