import { publicRequest, userRequest } from "../requestMethods";
import { loginFailure, loginStart, loginSuccess, setNotification } from "./userSlice";

export const login = async (dispatch, user) => {
  dispatch(loginStart());
  try {
    const res = await publicRequest.post("/auth/login", user);
    dispatch(loginSuccess(res.data));
    document.  location.reload();
    } catch (err) {
    dispatch(loginFailure());
  }
};


export const saveUser = async (dispatch, user,nav) => {
  dispatch(loginStart());
  try {
    const res = await userRequest.post("/user2", user);
    dispatch(setNotification({
      msg: "User Saved",
      error: false
    }));
    setTimeout(() => {
      nav("/users")
    }, 100)
  } catch (err) {
    dispatch(setNotification({
      msg: "Failed Try agin",
      error: true
    }));
  }
};

export const editUser = async (dispatch, user,nav,id) => {
  dispatch(loginStart());
  try {
    const res = await userRequest.put(`/user2/${id}`, user);
    dispatch(setNotification({
      msg: "User Saved",
      error: false
    }));
    setTimeout(() => {
      nav("/users")
    }, 100)
  } catch (err) {
    dispatch(setNotification({
      msg: "Failed Try agin",
      error: true
    }));
  }
};


export const deleteUser2 = async (dispatch,id) => {
  dispatch(loginStart());
  try {
    const res = await userRequest.delete(`/user2/${id}`);
    dispatch(setNotification({
      msg: "User Deleted",
      error: false
    }));
    return true
  } catch (err) {
    dispatch(setNotification({
      msg: "Failed Try agin",
      error: true
    }));
    return false
  }
};