function dynamicSort(property) {
  var sortOrder = 1;
  if(property[0] === "-") {
      sortOrder = -1;
      property = property.substr(1);
  }
  return function (a,b) {
      /* next line works with strings and numbers, 
       * and you may want to customize it to your needs
       */
      var result = (a[property] < b[property]) ? -1 : (a[property] > b[property]) ? 1 : 0;
      return result * sortOrder;
  }
}
export const andonSlice = createSlice({
  name: 'andon',
  initialState,
  reducers: {
    incrementByAmount: (state, action) => {
      state.value += action.payload
    },
    setchain: (state, action) => {
      console.log(action.payload);
      var a = state.chains.findIndex(chain => chain.name === action.payload.name) 
      if (a !== -1) {
        state.chains[a] = action.payload
      }else{
        state.chains.push(action.payload)
      }
    },
    updateChains: (state, action) => {
      state.chains = action.payload.map(chain => {
        return chain.data
      }).sort(dynamicSort("name"));
    },
  },
})

export const { incrementByAmount,setchain,updateChains } = andonSlice.actions

export default andonSlice.reducer