import React from "react";
import mqtt from 'mqtt/dist/mqtt';
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setchain, updateChains } from "../store/andonSlice";
import { Fab, TextField } from "@mui/material";
import SettingsIcon from '@mui/icons-material/Settings';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import { setsettings } from "../store/userSlice";
import WifiIcon from '@mui/icons-material/Wifi';
import WifiOffIcon from '@mui/icons-material/WifiOff';
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};
export default function Mqtt() {
  const settings = useSelector((state) => state.user.settings);
  const [connectionStatus, setConnectionStatus] = React.useState(false);
  const [messages, setMessages] = React.useState([]);
  const [first, setfirst] = React.useState(true);
  const [connected, setConnected] = React.useState(false);
  const dispatch = useDispatch()
  
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  
  useEffect(() => {
    if(!settings?.server)
    return
    const client = mqtt.connect(`wss://${settings.server}:${settings.port}/mqtt`,{
      username :"emqx",
      password:"public"
    });
    client.on('connect', () => {
      setConnectionStatus(true)
      client.subscribe(`${settings.topic}`, function (err) {
        if (!err) {
          console.log(`${settings.topic}`);
        }
      })
      client.subscribe(`${settings.topic}/all`, function (err) {
        if (!err) {
          console.log(`${settings.topic}/all`);
          if(first){
            client.publish(`${settings.topic}/data`, 'Hello mqtt')
          }
        }
      })
    }
      )
      ;
    client.on('message', (topic, payload, packet) => {
      if(topic === `${settings.topic}/all`){
        console.log(payload.toString());
        dispatch(updateChains(JSON.parse(payload.toString())));
        setfirst(false)
      }else if(topic === `${settings.topic}`){
        setMessages(messages.concat(payload.toString()));
        dispatch(setchain(JSON.parse(payload.toString())))
      }
    });
  }, [settings]);
  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log({
      server: data.get('server'),
      port: data.get('port'),
      topic: data.get('topic'),
    });
    dispatch(setsettings({
        server: data.get('server'),
        port: data.get('port'),
        topic: data.get('topic'),
        connected:true
      }))

      document.location.reload();
    // setSettings({
    //   server: data.get('server'),
    //   port: data.get('port'),
    //   topic: data.get('topic'),
    // })
  }

  return settings && (
    <div>
      <div  className='fab'>
        <Fab color="primary" aria-label="Settings" onClick={handleOpen}>
        <SettingsIcon />
      </Fab>
      <div className="coned" >
      {connectionStatus ?(<div className="status2 g">
      Connected
        <WifiIcon />
      </div> ):(<div className="status2 r">
      Disconnected
        <WifiOffIcon />
      </div> )}
      </div>

      </div>

      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
        <Typography component="h1" variant="h5" className='h11 t11'>
            Settings <SettingsIcon />
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              label="Broker Name"
              name="server"
              defaultValue={settings.server}
              autoFocus
              style={{width: "65%"}}
            />
            <TextField
              margin="normal"
              required
              name="port"
              label="port"
              style={{width: "35%"}}
              defaultValue={settings.port}
            />
            <TextField
              margin="normal"
              required
              name="topic"
              label="Topic Name"
              defaultValue={settings.topic}
              fullWidth
            />
            {connectionStatus ?"connected":"disconnected"}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              className="blue"
            >
              Save
            </Button>
          </Box>
        </Box>
      </Modal>


     
    </div>
  )
}




