import { Grid } from '@mui/material'
import React from 'react'
import Details from './Details'
import Siren from './Siren'

function Chaine({data}) {
    const getColor = (data) => {
      let color = ["darkblue","#c9c9c9","darkred","gold"]
      let al =[data.AP,data.AM,data.AQ,data.AL]
      const max = Math.max(...al);

      const index = al.indexOf(max);
        if (al[index] === 0)
        return "darkgreen";

        return color[index]
    }
  return (
    <Grid sm={39} lg={19} md={19} xl={13} style={{position:'relative',marginTop:20}}>
        <div className="title2">{data.name}</div>
        <div className='flexv2'>
            <Siren width={"25%"} color={getColor(data)} name={data.name} level={Math.max(data.AP,data.AM,data.AQ,data.AL)} />
            <Details width={"74%"} data={data}/>

        </div>
    </Grid >
  )
}

export default Chaine