import logo from './logo.svg';
import './App.css';
import { Routes, Route, Link, Navigate } from "react-router-dom";
import Login from './pages/Login';
import Home from './pages/Home';
import NavBar from './components/NavBar';
import NavBar2 from './components/NavBar2';
import { ThemeProvider } from '@mui/material';
import { darkTheme } from "./themes/Dark";
import Mqtt from './components/Mqtt';
import NotificationAndLoading from './components/NotificationAndLoading';


import Users from './pages/Users';
import AddUser from './pages/AddUser';
import EditUser from './pages/EditUser';
import NotFound from './pages/NotFound';
import Footer from './components/Footer';
import { useSelector } from 'react-redux';

import BackButton from './components/BackButton';
import His from './pages/His';
import Login2 from './pages/Login2';



function App() {
  const user = useSelector((state) => state.user);

  return (
    <div className="App">
      <Mqtt />
      <ThemeProvider theme={darkTheme}>
        <NotificationAndLoading />
        {
          // user.logedin && <NavBar2 />
          user.currentUser.accessToken && <NavBar />
        }
        <BackButton />
        <Routes>

           
           {/* <Route path="/" element={user.logedin ? <Home /> : <Navigate replace to="/login" />} />
          <Route path="/login" element={!user.logedin ? <Login2 /> : <Navigate replace to="/" />} />  */}


           <Route path="/" element={user.currentUser.accessToken != null ? <Home /> : <Navigate replace to="/login" />} />
         <Route path="/NotFound" element={<NotFound />} />
          <Route path="login" element={user.currentUser.accessToken == null ? <Login /> : <Navigate replace to="/" />} />
          <Route path="/users" element={user.currentUser.accessToken ? <Users /> : <Navigate replace to="/NotFound" />} />
          <Route path="/his" element={user.currentUser.accessToken ? <His /> : <Navigate replace to="/NotFound" />} />
          <Route path="/users/add" element={user.currentUser.accessToken ? <AddUser /> : <Navigate replace to="/NotFound" />} />
          <Route path="/users/edit/:id" element={user.currentUser.accessToken ? <EditUser /> : <Navigate replace to="/NotFound" />} />


        </Routes>
        <Footer />
      </ThemeProvider>
    </div>
  );
}

function About() {
  return (
    <>
      <main>
        <h2>Who are we?</h2>
        <p>
          That feels like an existential question, don't you
          think?
        </p>
      </main>
      <nav>
        <Link to="/">Home</Link>
      </nav>
    </>
  );
}

export default App;
