import { createSlice } from '@reduxjs/toolkit'
import axios from 'axios'
import {  userRequest } from '../requestMethods'
import sha256 from 'crypto-js/sha256';
import Base64 from 'crypto-js/enc-base64';
const initialState = {
  currentUser:{
    },
    notification:{
      msg:"",
      error:false
    }
    ,loading:false,
    logedin:false,
    settings:{
      server : "broker.emqx.io",
      port : 8084,
      topic : "/safta/c2i/mqtt",
    }
}

export const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    loginFailure: (state, action) => {
      state.loading = false
      state.notification = {
        msg:"Loging Failed ",
        error:true
      }
    },
    loginStart: (state, action) => {
      state.loading = true
    },
    login2: (state, action) => {
      if(action.payload.email === "andon@c2i.tn"  && Base64.stringify(sha256(action.payload.password)) === "HeAlIkjyx6bl99FvD/OAxSwsUyzNm6dKuRQm5hqYIck="){
        state.logedin = true
      }
    },
    loginOut: (state, action) => {
      state.currentUser = {}
      state.notification = {
        msg:"Logged Out ",
        error:false
      }
      userRequest.defaults.headers.common['token'] = ``;

    },
    loginSuccess: (state, action) => {
      state.loading = false
      state.notification = {
        msg:"Welcome Agin ",
        error:false
      }
      state.currentUser = action.payload
      axios.defaults.headers.common['token'] = `Bearer ${state.currentUser.accessToken}`;

    },
    setNotification: (state, action) => {
      state.loading = false
      state.notification = action.payload
      
    },
    setLoading: (state, action) => {
      state.loading = action.payload      
    },
    setsettings: (state, action) => {
      state.settings = action.payload      
    },

  },
})

export const { loginFailure,setsettings, loginStart,setLoading, loginSuccess,setNotification,loginOut ,login2} = userSlice.actions

export default userSlice.reducer