import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Container } from '@mui/system';
import { Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import { userRequest } from '../requestMethods';

import EditIcon from '@mui/icons-material/Edit';
import SearchAddBar from '../components/SearchAddBar';
import { useNavigate } from 'react-router';
import { setLoading, setNotification } from '../store/userSlice';
import { useDispatch } from 'react-redux';
import DeleteIcon from '@mui/icons-material/Delete';
import { deleteUser2 } from '../store/apiCalls';

// import { jsPDF } from "jspdf";
// import html2PDF from "jspdf-html2canvas";

// Default export is a4 paper, portrait, using millimeters for units


export default function Users() {

    const  getLevel = (level) => {
        switch (level) {
            case "1": return "Technicien"
            case "2": return "Superviseur"
            case "3": return "Dous-Directeur"
            case "4": return "Directeur"
        }
    }
    const  getType = (post) => {
        switch (post) {
            case "1": return "Qualite"
            case "2": return "Maintenance"
            case "3": return "Production"
            case "4": return "Logistique"
        }
    }
    const [users, setUsers] = React.useState([])
    const [search, setsearch] = React.useState("")
    const navigate = useNavigate();
    const dispatch = useDispatch();
    // function pdf() {
    //     html2PDF(document.getElementById("InvoiceTemp"), {
    //         jsPDF: {
    //           format: "a4"
    //         },
    //         html2canvas: {
    //           imageTimeout: 15000,
    //           scale: 5,
    //           logging: true
    //         },
    //         imageQuality: 0.9,
    //         imageType: "image/jpeg",
    //         output: "generate.pdf"
    //       });
    // }
    // function pdf2() {
    //    window.print()
    // }

    const getData = async (uri) => {
        dispatch(setLoading(true));
        try{
            const { data } = await userRequest.get(uri);
            setUsers(data);
            dispatch(setLoading(false));

        }catch(e){
            dispatch(setLoading(false));
        }
    };
    function searchH(text) {
        setsearch(text)
    }

    React.useEffect(() => {
        if(search.length >= 3){
            getData(`/user2/search/${search}`);
        }else if(search.length === 0){
            getData(`/user2?new`);
        }
    }, [search]);

    const [open, setOpen] = React.useState(false);
    const [id, setId] = React.useState("");

    const handleClickOpen = (id) => {
        setOpen(true);
        setId(id)

    };
    const deleteAgree = () => {
        deleteUser2(dispatch, id).then(d => {
            setUsers(users.filter(t => t._id != id))
            setId("")
        }).catch(e => console.log(e));
        setOpen(false);
    }

    const handleClose = () => {
        setOpen(false);
        setId("")
    };
    return (
        <Container component="main" maxWidth="xl" id="InvoiceTemp" >
                        <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {"Are you sure to remove this User?"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                    This User will be permanently deleted and will not be refunded
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Disagree</Button>
                    <Button onClick={deleteAgree} autoFocus>
                        Agree
                    </Button>
                </DialogActions>
            </Dialog>
            <Box
                sx={{
                    marginTop: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <h1 className='title' >Super visieurs</h1>
                {/* <Button style={{backgroundColor: "orange"}} onClick={e => pdf()} variant="contained" endIcon={<EditIcon />}>
                                        pdf
                                    </Button>
                <Button style={{backgroundColor: "blue"}} onClick={e => pdf2()} variant="contained" endIcon={<EditIcon />}>
                                        pdf2
                                    </Button> */}
                <SearchAddBar searchHandler={searchH} uri="/users/add"/>
                <TableContainer component={Paper}>
                    <Table sx={{ minWidth: "80%" }} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center">Name</TableCell>
                                <TableCell align="center">Type</TableCell>
                                <TableCell align="center">Level</TableCell>
                                <TableCell align="center">Post</TableCell>
                                <TableCell align="center">Phone</TableCell>
                                <TableCell align="center">Created At</TableCell>
                                <TableCell align="center">Edit</TableCell>
                                <TableCell align="center">Delete</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {users.map((row) => (
                                <TableRow
                                    key={row._id}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {row.name}
                                    </TableCell>
                                    <TableCell align="center">{getType(row.post)}</TableCell>
                                    <TableCell align="center">{getLevel(row.level)}</TableCell>
                                    <TableCell align="center">Post {row.p}</TableCell>
                                    <TableCell align="center">{row.tel}</TableCell>
                                    <TableCell align="center">{row.createdAt}</TableCell>
                                    <TableCell align="center"><Button style={{backgroundColor: "orange"}} onClick={e => navigate(`/users/edit/${row._id}`)} variant="contained" endIcon={<EditIcon />}>
                                        Edit
                                    </Button>
                                    </TableCell>
                                    <TableCell align="center"><Button style={{backgroundColor: "red"}} onClick={e => handleClickOpen(row._id)} variant="contained" endIcon={<DeleteIcon />}>
                                        Delete
                                    </Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Box>
        </Container>
    );
}
