import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import {  saveUser } from '../store/apiCalls';
import { useDispatch } from "react-redux";
import { Checkbox, FormControlLabel, MenuItem, Select } from '@mui/material';
import { useNavigate } from 'react-router';
import { useState } from 'react';

export default function AddUser() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [level, setlevel] = useState("1")
  const [post, setpost] = useState("1")
  const [p, setP] = useState("A")

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);

    console.log({
        name: data.get('name'),
        post: post,
        level: level,
        p: p,
        tel: data.get('tel'),
      });
      saveUser(dispatch,{
        name: data.get('name'),
        post: post,
        level: level,
        p: p,
        tel: data.get('tel'),
      },navigate)
  };
  

  return (
      <Container component="main" maxWidth="md">
        <Box
          sx={{
            marginTop: 1,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
            <h1 className='title' >Add New Super Visior</h1>
          <Box component="form" onSubmit={handleSubmit}  sx={{ mt: 1 }}>

            <TextField
              margin="normal"
              required
              fullWidth
              id="name"
              label="Name"
              name="name"
              autoComplete= "no"

              inputProps={{
                autoComplete: ''
             }}
            />
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={post}
              label="Post"
              name="post"
              onChange={e => setpost(e.target.value)}
            >
              <MenuItem value={1}>Qualite </MenuItem>
              <MenuItem value={2}>Maintenance</MenuItem>
              <MenuItem value={3}>Production</MenuItem>
              <MenuItem value={4}>Logistique</MenuItem>
            </Select>
            <TextField
              margin="normal"
              required
              fullWidth
              name="tel"
              label="Phone Number"
              id="tel"
            />

            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={level}
              label="Age"
              name="level"
              onChange={e => setlevel(e.target.value)}
            >
              <MenuItem value={1}>Technicien</MenuItem>
              <MenuItem value={2}>Superviseur</MenuItem>
              <MenuItem value={3}>Sous-Directeur</MenuItem>
              <MenuItem value={4}>Directeur</MenuItem>
            </Select>

             <div>

            {   (parseInt(level) < 3) && (<Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={p}
              label="Post"
              name="level"
              onChange={e => setP(e.target.value)}
            >
              <MenuItem value={"A"}>A</MenuItem>
              <MenuItem value={"B"}>B</MenuItem>
              <MenuItem value={"C"}>C</MenuItem>
            </Select>)}
             </div>

            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              className="blue"
            >
              Save
            </Button>
          </Box>
        </Box>
      </Container>
  );
}
